"use client";
import { useAuth } from "@/components/AuthContext";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { useRouter } from "next/navigation";
import { useEffect, useState } from "react";
import { userService } from "@/lib/services/userService";
import { error } from "console";
import { User } from "@/type/user";
import {
  Pagination,
  PaginationContent,
  PaginationItem,
  PaginationLink,
  PaginationNext,
  PaginationPrevious,
} from "@/components/ui/pagination";
import { Button } from "@/components/ui/button";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import { Separator } from "@/components/ui/separator";
import UserForm from "@/components/user/UserForm";

export default function Page() {
  const { user, isAuthenticated } = useAuth();
  const router = useRouter();
  const [usersData, setUsersData] = useState<User[]>([]);
  const [page, setPage] = useState<number>(1);
  const [totalUser, setTotalUser] = useState<number>(0);
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [search, setSearch] = useState<string>("");
  const [open, setOpen] = useState<boolean>(false);
  const [userDetail, setUserDetail] = useState<User>();
  const [actionType, setActionType] = useState<string>("add");
  useEffect(() => {
    if (user?.role != "admin") {
      router.push("/dashboard");
    }

    if (!isAuthenticated) {
      router.push("/login");
    }
  }, [user, isAuthenticated]);

  useEffect(() => {
    getUserData();
  }, [page, search]);

  const getUserData = () => {
    setIsLoading(true);
    userService
      .getUsers(search)
      .then((res) => {
        setIsLoading(false);
        setUsersData(res.data.data.results);
        setTotalUser(res.data.data.count);
      })
      .catch((error) => {
        setIsLoading(false);
      });
  };

  const handleNextPage = () => {
    if (page < totalUser / 10) {
      setPage((prev) => prev + 1);
    }
  };
  const handlePrevPage = () => {
    if (page > 1) {
      setPage((prev) => prev - 1);
    }
  };

  return (
    <div className="w-full">
      <div className="flex justify-between items-center px-2 lg:px-12 py-4">
        <input
          className="max-w-md bg-white px-4 py-3 border border-gray-300 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
          placeholder="search"
          onChange={(e) => setSearch(e.target.value)}
        />

        <Dialog open={open} onOpenChange={setOpen}>
          <DialogTrigger className="bg-black hover:bg-gray-500 cursor-pointer text-white px-4 py-2 rounded-lg shadow-lg">
            Add Book
          </DialogTrigger>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>Add Book</DialogTitle>
            </DialogHeader>
            <Separator />
            <UserForm
              setOpen={setOpen}
              getUsers={getUserData}
              actionType={actionType}
              userDetail={userDetail}
            />
          </DialogContent>
        </Dialog>
      </div>
      <div className="flex flex-col justify-center px-2 lg:px-12">
        <Table className="bg-white">
          <TableHeader>
            <TableRow>
              <TableHead>User Id</TableHead>
              <TableHead>User Name</TableHead>
              <TableHead>Email</TableHead>
              <TableHead>Role</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {usersData &&
              usersData.map((user: User, index) => (
                <TableRow key={index}>
                  <TableCell>{user.id}</TableCell>
                  <TableCell>{user.username}</TableCell>
                  <TableCell>{user.email}</TableCell>
                  <TableCell>{user.role}</TableCell>
                </TableRow>
              ))}
          </TableBody>
        </Table>
      </div>
      <Pagination className="self-start">
        <PaginationContent>
          <PaginationItem onClick={isLoading ? undefined : handlePrevPage}>
            <PaginationPrevious />
          </PaginationItem>
          {totalUser > 0 && (
            <>
              {
                <PaginationItem>
                  <PaginationLink>Total:{totalUser}</PaginationLink>
                </PaginationItem>
              }
            </>
          )}

          <PaginationItem
            onClick={isLoading ? undefined : handleNextPage}
            className={
              usersData.length === 0 ? "pointer-events-none opacity-50 " : ""
            }
          >
            <PaginationNext />
          </PaginationItem>
        </PaginationContent>
      </Pagination>
    </div>
  );
}
